.planet-ilustration {
  display: flex;
  margin: auto;
  max-width: 990px;
  align-items: center;
  justify-content: center;
  position: relative;
}

@media screen and (max-width: 866px) {
  .planet-ilustration {
    min-height: 460px;
  }
}

@media screen and (max-width: 700px) {
  .planet-ilustration {
    min-height: 304px;
  }
}
